import { useState } from 'react';
import {ReactComponent as Arrow} from '../assets/arrow.svg'
import store from '../utils/store/reducers'
import { useSelector} from 'react-redux'

export const coinState = {
    name : '0,15 ETH',

}

const buttonValueState = {
    value: 'ETH',
}

const MintButton = ({datos,stylesCss,caso, popup, closepopup,functions, buttonPrice, popUpUniversal}) => {

    const [openMenu, setOpenMenu] = useState(false);
    const [buttonName, setButtonName] = useState(coinState)
    const [buttonValue, setButtonValue] = useState(buttonValueState)
    
    const blockchain = useSelector((store)=> store.blockchain)

    const prices = [buttonPrice.priceETH, buttonPrice.priceMatic]

    const handleMenu = (i)=> {
        setOpenMenu(i)
        if (openMenu === i )setOpenMenu(false)
    }

    console.log('POP UNIVERSAL@')
    console.log(popUpUniversal)

    return ( <>
    <div className="botones_mint">
            <button className='boton --boton__mint --botonNFT'
            onClick={()=>{
                if(blockchain.account && buttonValue.value == 'ETH'){
                    functions.minter(blockchain.account)
                    popup({
                        bool: true,
                        mensaje: 'LOADING...'
                    })
                }else if(blockchain.account && buttonValue.value == 'MATIC'){
                    functions.allowance(blockchain.account)
                    popup({
                        bool: true,
                        mensaje: 'LOADING...'
                    })
                }else{
                    popup({
                        bool: true,
                        mensaje: 'Connect your Metamask'
                    })
                    closepopup()
                }
            }}
            >MINT NFT</button>
            {caso ? datos.map((e,i)=>{
            return (    
                <div key={i} className={stylesCss} >
                    <div className={openMenu !==i ? "menuComponet__header --borderRadio" : 'menuComponet__header '} onClick={()=>{handleMenu(i)}}>
                        <div className='menuComponet__title'> {buttonName.price}{buttonName.name}</div>
                        {popUpUniversal.bool == false ? (   
                                <div className={openMenu !==i  ? " menuComponet__arrow --arrow-down" : 'menuComponet__arrow'}><Arrow/></div>
                        
                        ) : ('')}
                    </div>
                    <div className={openMenu ===i ? 'menuComponet__body --active ' : 'menuComponet__body --disable'}>
                        {e.content.map((b,y)=><button key={y} value={b} className='button_mint'onClick={(e)=>{console.log(e.target.value)
                            if(e.target.value == 'ETH'){
                                
                                setButtonName({
                                    name:' ETH',
                                    price : buttonPrice.priceETH
                                })
                                setButtonValue({
                                    value: ' ETH',
                                })
                                setOpenMenu(false)
                            }else {
                                console.log('Seleccione Matic')
                                setButtonName({
                                    name:' MATIC',
                                    price:buttonPrice.priceMatic
                                })
                                setButtonValue({
                                    value:' MATIC',
                                })
                                setOpenMenu(false)
                            } 
                        }} > {prices[y]} {b}</button>)}
                    </div>
                </div>
            )
        })
        :
        datos.map((e,i)=>{
            return (
                <div key={i} className={stylesCss} >
                    <div className='menuComponet__header' onClick={()=>{handleMenu(i)}}>
                        <div className='menuComponet__title'>{e.name}</div>
                        <div className={openMenu !==i ? " menuComponet__arrow --arrow-down" : 'menuComponet__arrow'}><Arrow/></div>
                    </div>
                    <div className={openMenu ===i ? 'menuComponet__body --active ' : 'menuComponet__body --disable'}>
                        <p className='menuComponet__body__text'>{e.content}</p>
                    </div>
                </div>
            )
        })
        }
        </div>
        </> );
}

export default MintButton;