import About from "../componets/About";
import Faqs from "../componets/Faqs";
import Marketplace from "../componets/Marketplace";
import Mint from "../componets/Mint";
import NftsBenefits from "../componets/NftsBenefits";
import Roadmap from "../componets/Roadmap";
import OubitaToken from "../componets/OubitaToken";
import { useEffect, useState } from "react";
import { ancla } from "../componets/Menu";

const Home = () => {

    useEffect(() => {
        let element = document.getElementById(ancla)
        if(element){
            element.scrollIntoView()
        }
    }, []); 

    return (
        <main className='content'>
            <Mint id={'mint'}/>
            <About id={'about'}/>
            <NftsBenefits id={'rewards'}/>
            <Roadmap id={'roadmap'}/>
            <Marketplace id={'marketplace'}/>
            <OubitaToken id={'OUBITATOKEN'}/>
            <Faqs id={'faqs'}/>
        </main>
    );
}
 
export default Home;