import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { createUserServer, getOnlyUser } from '../apis/requestApi';
import Logo from '../assets/png/nft-logo.png'
import NftRender from '../componets/NftRender';
import store from '../utils/store/reducers'
import { useDispatch, useSelector } from 'react-redux';
import { conexion } from '../utils/store/slices/blockchainSlice';
import { fetchData } from '../utils/store/slices/dataSlice';
import { prueba } from '../utils/store/slices/dataSlice';


const Profile = () => {

    let dispatch = useDispatch()
    
      const [datosUser, setDatosUser] = useState({
          username: localStorage.getItem('account'),
          name: '',
          email: ''
      });

      const [message, setMessage] = useState(null);
    

      useEffect(() => {
          let account = handleLogin()
          getDatosUserServer(account)
      }, [])

      const blockchain = useSelector((store)=>store.blockchain)
      const dataUser = useSelector((store)=>store.dataUser)
  

      let LocalAccount = blockchain.account
      let navigate = useNavigate();
    
      function checkWalletConnection() {
        if(LocalAccount.length > 3) {
          return {status:true, account: LocalAccount}
        }
        else {
          return {status:false, account: null}
        }
      }
    
      const checkVerify = (res) => {
        if(res.status) {
          navigate("/profile")
        }else {
          navigate("/")
        }
      }
    
      const handleLogin =  () => {
        let res = checkWalletConnection(LocalAccount)
        checkVerify(res)
        return res.account
      }

      async function getDatosUserServer(account) {
        try {
          let res = await getOnlyUser(account)
          console.log(res)
        } catch (error) {
          console.log(error)
        }
      }
      function getDatosUserForm (event) {
        let user = datosUser
        const { name, value } = event.target
        let newUser = {...user,[name]: value}
        setDatosUser(newUser)
      }

      function beforeCreatingValidateDataUser(datosUser) {
            const regExpCorrero = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i
          if(datosUser.username.length === 0) {
              throw {
                  status:false,
                  message :'no existe su identificador de usuario por favor vuelva a loguearse'
              }
          }
          if(datosUser.name === '') {
            throw {
                status: false,
                message:'el campo del nombre no puede estar vacio'
            }
          }
          if(datosUser.name.length <=3 ){ 
            throw {
                status: false,
                message: 'el campo nombre debe tener mas de 3 caracteres'
            }
          }
          if(!regExpCorrero.test(datosUser.email) || datosUser.email.length === 0){
            throw {
                status: false,
                message: 'el campo email debe ser valido'
            }
          }
      }
      async function hadleDatosUser() {
        try {
            let user = datosUser
            let res
            res = beforeCreatingValidateDataUser(user)
            res = await createUserServer(user)
            setMessage(res)
        } catch (error) {
            let res = error
            setMessage(res)
        }
      }

      useEffect(()=>{
        
        prueba(blockchain.account, dispatch, blockchain.contracNFT)
        
        const interval = setInterval(()=>{
          console.log('Funcionando')
          prueba(blockchain.account, dispatch, blockchain.contracNFT)
        }, 10000)

        return()=>clearInterval(interval)
      },[])

      

    return ( 
        <div className='profile'>
            <div className='profile__header'>
                <h1>{message?.message}</h1>
                <div className='profile__ContainerImg'>
                    <img src={Logo} className = 'img' alt="" />
                </div>
                <div className='profile__Wallet'>
                  {datosUser.username} 
                </div>
                <div className='profile__form'>
                    <input className='profile__input' type="text"  placeholder='Nombre y Apellido'
                        name='name'
                        onChange={(e)=>getDatosUserForm(e)}
                    />
                    <input className='profile__input' type="email"  placeholder='E-mail'
                        name='email'
                        onChange={(e)=>getDatosUserForm(e)}
                    />
                    <button className='boton --form__botton'
                        onClick={()=>hadleDatosUser()}
                    >Save</button>
                </div>
            </div>
            <div className='profile__body'>
                <h1 className='profile__body_titile'>My NFTs</h1>
            </div>
            <div className='profile_nft_container'>
                <NftRender data={dataUser}/>
            </div>
        </div>
     );
}
 
export default Profile;