import React, { useState } from 'react'
import opensea from '../assets/png/opensea.png'
import rarible from '../assets/png/rarible.png'
import capa1 from '../assets/png/capa2.png'

const marketPlaceData = [
  {
    title : 'OPENSEA',
    content : `Once an NFT is minted it will appear at The Oubita Collection in the Opensea and Rarible
      marketplace. Anyone who sales a Oubita NFT will have a 5% OUbita Fee for the presale.`,
      img :opensea,
  },
  {
    title : 'RARIBLE',
    content : `Once an NFT is minted it will appear at The Oubita Collection in the Opensea and Rarible
      marketplace. Anyone who sales a Oubita NFT will have a 5% OUbita Fee for the presale.`,
    img :rarible,
  },
  
]


const Marketplace = ({id}) => {
  const [marketplace, setMarketplace] = useState(marketPlaceData);
  return (
    <div className='marketplace' id={id}>
        <img src={capa1} className="mancha11"alt="" />

      <h1 className='marketplace__title'>MARKETPLACE</h1>
      {marketplace.map((e,i)=>{
        return (
        <div className='marketplace__card' key={i}>
          <div className={ i !== 1 ?'marketplace__card__content' : 'marketplace__card__content --order2'}>
            <h1 className='marketplace__card__content__title'>{e.title}</h1>
            <p className='marketplace__card__content__copy'>{e.content}</p>
            <button className='boton --marketplace__button'>LEARN MORE</button>
          </div>
            <img className='marketplace__card__figure_img' src={e.img} alt="" />
        </div>
        )
      })}
      <div className='boton --marketplace__footer  '>
        There will be a premarket with a lower price 0,15ETH for the First 2,000 sales. Later 
        on the set price will be 0,20 ETH.
      </div>
    </div>
  )
}

export default Marketplace