import React, { useEffect, useRef, useState } from 'react'
import roadMapQ1 from '../assets/png/Roadmap_q1_pc.png' 
import roadMapQ3 from '../assets/png/Roadmap_q3_pc.png' 
import roadMapMobileQ1 from '../assets/png/Roadmap-q-1.png' 
import roadMapMobileQ3 from '../assets/png/Roadmap-q-3.png' 
import { ReactComponent as Arrow} from '../assets/arrow.svg'
import { ReactComponent as IconPlay} from '../assets/playVideo.svg'
import capa4_2 from '../assets/png/capa3-1.png'
import recorte from '../assets/png/capa3-1Recorte.png'
import capa4_3 from '../assets/png/capa2.png'


const Roadmap = ({id}) => {
  const roadMap = [
    {
      nombre : 'Q2 2022',
      parrafos : [
        'DEVELOPMENT AND DESING OF THE NFTs',
        'LAUNCH OUBITA NFTs',
        'FIRST AWARD ON THE OUBITA EXCHANGE PLATFORM',
        'FREE ENTRY OUBITA METAVERSE AT DECENTRALAND',
        'LAUNCH GAMES AND TOURNAMENTS (DISCORD)',
        'MARKETING CAMPAING',
        'LAUNCH OF THE OUBITA PAYMENT CARD',
        'SURPRISE REWARDS TO THOSE WHO HOLD 5 OR MORE OUBITA NFTs',
        'NFTs GALERY IN THE OUBITA SPACE OF THE METAVERSE',
        'LAUNCH OUBITA TOKEN'
      ]
    },
    {
      nombre : 'Q3 2022',
      parrafos : [
        'EVENTS AND PARTTIES IN THE METAVERSE IN THE OUBITA SPACE',
        'OUBITA TOKEN STAKING SECTION THROUGH OUBITA NFTs',
        'OUBITA MINIGAMES LAUNCH AT METAVERSE',
        'LAUNCH CRYPTOS STAKING POOL',
        'LAUNCH LAB GAME',
        'NEW UTILITY NFTs AND PLAY TO EARN',
      ]
    },
    {
      nombre : 'Q4 2022',
      parrafos : [
        'OWN OUBITA NFT MARKETPLACE',
        'NEW PARTNERS AND CELIBRITY EVENTS AND PARTIES',
      ]
    },
    {
      nombre : 'Q1 2023',
      parrafos : []
    },
  ]
  const [isActiveArrow, SetIsActiveArrow] = useState(false);
  const [array, setArray] = useState([]);
  const refVideo = useRef()
  const [isPlaying, setIsPlaying] = useState(false);

  useEffect(() => {
    filtarArray(false)
  }, []);

  function filtarArray(isActiveArrow) {

    if(isActiveArrow) {
      setArray(roadMap)      
    }else {
      let rest = roadMap.filter((e,i)=> i<=1)
      const aux = [...rest, ...rest[1].parrafos = []]
      setArray(aux)
    }
    SetIsActiveArrow(isActiveArrow)

  }

  function handleVido() {
    const video = refVideo.current
    setIsPlaying(!isPlaying)

    isPlaying ? video.pause() : video.play() 
  }
  return (
    <div className='roadmap' id={id}>
      <img src={capa4_2} className="mancha6"alt="" />
      <img src={recorte} className="mancha6recorte"alt="" />
      <img src={capa4_2} className="mancha7"alt="" />
      <img src={capa4_2} className="mancha8"alt="" />
      <img src={capa4_2} className="mancha9"alt="" />
      <img src={capa4_3} className="mancha10"alt="" />


      <div className="roadmap__header">
        <h1 className='roadmap__header__title'>ROADMAP</h1>
      </div>
      <div className='roadmap__body'>
        
        <div className='roadmap__figure'>
          {array.map((e,i)=>{return (
            <div className='roadmap__render' key={i}>
              <h4 className={`parrafo${i}`}>{e.nombre}</h4>
              {
                e.parrafos.map((a,b)=>
                  <p className={`parrafo${i}-${b}`}  key={b}>{a}</p>)
              }
            </div>
          )})}
        <div className='roadmap_img_desktop'>
          <img src={roadMapQ1} className='img1' alt="" />
          <img src={roadMapQ3} className={ isActiveArrow ? 'img2 --img2Visible' : 'img2 '} alt="" />
        </div>
        <div className='roadmap_img_mobile'>
          <img src={roadMapMobileQ1} className='img1' alt="" />
          <img src={roadMapMobileQ3} className={ isActiveArrow ? 'img2 --img2Visible' : 'img2 '} alt="" />
        </div>
        </div>

        <button  onClick={()=>filtarArray(!isActiveArrow)} className="boton --botonRoadMap">
              READ MORE <Arrow className={isActiveArrow ? 'general__arrow' : 'general__arrow  --arrow-down' }/> 
        </button>

        <div className='roadmap__body__section'>
          <h1 className='roadmap__body__title'>OUBITA'S METAVERSE</h1>
          <div className='roadMap__video' onClick={()=>handleVido()}>
            <video className='video' ref={refVideo} >
                <source src='/videos/video1.mp4'/>
            </video>
            {!isPlaying &&  <IconPlay className='iconPlay'/>}
          </div >
          <p className='roadmap__video__descripcion'>
            Oubita's space in the Descentraland metaverse has been designed to provide thousands of experiences. One of the main advantages is the 
            number of events that we are going to provide, from celebrities to knowledge and learning events. On the other hand, there will be a social
            space dedicated totally to enjoy events and parties. Also worth highlighting is our games room that will allow club members to play "play to gain" game.
            One of the most outstanding advantages is the access to the hot news from Oubita that can make your position as a partner very advantageous since you 
            will ve able to anticipate manu tools before the rest. Do not be impatient, you will see very powerful things in this space. just sit back and watch
          </p>
        </div>
      </div>

    </div>
  )
}

export default Roadmap