import { createSlice } from "@reduxjs/toolkit";
import { ethers} from "ethers";
import ContratoNFT from "../../../contracts/abi.json"
import ContractToken from "../../../contracts/token_abi.json"
import { fetchData } from "./dataSlice";


// ESTADO INICIAL Y CAMBIO DE ESTADO EN REDUCERS 

const blockchainSlice = createSlice({
    name:'blockchain',
    initialState:{
        login: false,
        account: '',
        contracNFT : null ,
        contratoTok : null,
            errorMsg: ""
    },
    reducers: {

        conectando: (state, action)=> {
            state.login = action.payload.login
        
        },

        conectado: (state, action)=>{
            state.login = action.payload.login
            state.account = action.payload.account
            state.contracNFT = action.payload.contratoNFT
            state.contratoTok = action.payload.contratoToken

        }
    }
})


// ACTIONS 

const {conectando, conectado} = blockchainSlice.actions


export const conexion= ()=>{

    return async (dispatch)=>{

        dispatch(conectando({login:true}))

        try {

           //CONECTAR A METAMASK Y ADQUIRIR NUMERO DE RED

            const account = await window.ethereum.request({
            method: "eth_requestAccounts"
            })

            const networkId = await window.ethereum.request({
            method: "net_version"
            })

            
            // GET CONTRATO 

            const contractAddress = '0x0685592357441D9dA3F1174EDc73e72dE7b2edD4'
            const tokenContratAddress = '0x1955D59b648489E1B431c7684706F33d68d7adA8'
            
            const Provider = await new ethers.providers.Web3Provider(window.ethereum)
            const signer = await Provider.getSigner()
            const NftContract = await new ethers.Contract(contractAddress, ContratoNFT, signer)
            const TokenContrato = await new ethers.Contract(tokenContratAddress, ContractToken, signer )

            //EXTRACION DATOS DEL CONTRATO 


        if(networkId == 4){
            

            dispatch(conectado({
            login: true,
            account: account[0],   
            contratoNFT: NftContract,
            contratoToken: TokenContrato  
        })) 

        
            dispatch(fetchData(account[0], dispatch, NftContract))
            
        } else{
            console.log('CONECTED TO THE WORONG NETWORK')
        }

            
        } catch (err) {
            console.log(err)
            console.log('it possible that you havent metamask in your browser')
            
        }
    }

}


export default blockchainSlice.reducer